h;\r\n}\r\n\r\n\r\n.vertical-content {\r\n    display: -moz-flex;\r\n    display: flex;\r\n    align-items: center;\r\n    -webkit-align-items: center;\r\n    justify-content: center;\r\n    -webkit-justify-content: center;\r\n    flex-direction: row;\r\n}\r\n\r\n\r\n.bg-overlay {\r\n    background: #005C97;  /* fallback for old browsers */\r\n    background: linear-gradient(to right, #363795, #005C97); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */\r\n    opacity: 0.95;\r\n    position: absolute;\r\n    height: 100%;\r\n    width: 100%;\r\n    right: 0;\r\n    bottom: 0;\r\n    left: 0;\r\n    top: 0;\r\n}\r\n\r\n\r\n.bg-overlay-light {\r\n    background-color: rgba(0, 0, 0, 0.5);\r\n}\r\n\r\n\r\n.section {\r\n    padding-top: 80px;\r\n    padding-bottom: 80px;\r\n    position: relative;\r\n}\r\n\r\n\r\n.section-sm {\r\n    padding: 50px 0;\r\n}\r\n\r\n\r\n.section-lg {\r\n    padding: 100px 0;\r\n}\r\n\r\n\r\n.section-title {\r\n    text-transform: uppercase;\r\n    letter-spacing: 2px;\r\n    margin-bottom: 0px;\r\n    padding-bottom: 1rem;\r\n    line-height: 1;\r\n    font-size: 1.75rem;\r\n    font-weight: 700;\r\n}\r\n\r\n\r\n.section-title-border {\r\n    width: 50px;\r\n    height: 2px;\r\n    margin-right: auto;\r\n    margin-left: auto;\r\n}\r\n\r\n\r\n.section-subtitle {\r\n    letter-spacing: 0.02em;\r\n}\r\n\r\n\r\n.svg-pattern {\r\n  width: 100%;\r\n  position: relative; \r\n}\r\n\r\n\r\niframe {\r\n    max-width: 100%;\r\n}\r\n\r\n\r\n.hover-effect {\r\n    transition: all .3s ease-in-out;\r\n}\r\n\r\n\r\n.hover-effect:hover {\r\n    transform: translateY(-1rem);\r\n}\r\n\r\n\r\n/*----BACKGROUND COLOR-----*/\r\n\r\n\r\n.bg-gray {\r\n    background-color: #f7f7f7;\r\n}\r\n\r\n\r\n.bg-white {\r\n    background-color: #fff;\r\n}\r\n\r\n\r\n.bg-dark {\r\n    background-color: #393f4f !important;\r\n}\r\n\r\n\r\n.bg-light {\r\n    background-color: #fafbfe!important;\r\n}\r\n\r\n\r\n/*=====================\r\n  3. BOOTSTRAP RESET\r\n  ======================*/\r\n\r\n\r\n/*--Text color--- */\r\n\r\n\r\n.text-muted {\r\n    color: #858d96 !important;\r\n}\r\n\r\n\r\n.text-white {\r\n    color: #fff !important;\r\n}\r\n\r\n\r\n.text-gray {\r\n    color: #777 !important;\r\n}\r\n\r\n\r\n.text-dark {\r\n    color: #212121 !important;\r\n}\r\n\r\n\r\n/*====Buttons=======*/\r\n\r\n\r\n.btn {\r\n    padding: 10px 22px;\r\n    transition: all 0.5s;\r\n    box-shadow: none !important;\r\n}\r\n\r\n\r\n.btn:hover {\r\n    transform: translateY(-5px);\r\n}\r\n\r\n\r\n.btn-rounded {\r\n    border-radius: 30px;\r\n}\r\n\r\n\r\n.btn-custom {\r\n    border: 1px solid;\r\n    color: #fff;\r\n    transition: all 0.3s;\r\n    font-size: 14px;\r\n}\r\n\r\n\r\n.btn-custom:hover,\r\n.btn-custom:focus,\r\n.btn-custom:active,\r\n.btn-custom.active,\r\n.btn-custom.focus,\r\n.btn-custom:active,\r\n.btn-custom:focus,\r\n.btn-custom:hover,\r\n.open > .dropdown-toggle.btn-custom {\r\n    border: 1px solid;\r\n    color: #f3f3f3;\r\n}\r\n\r\n\r\n.btn-bg-white {\r\n    background-color: #fff !important;\r\n    transition: all 0.3s;\r\n    border: 2px solid #fff;\r\n    color: #000 !important;\r\n    font-size: 14px;\r\n}\r\n\r\n\r\n.read-btn {\r\n    font-size: 15px;\r\n}\r\n\r\n\r\n.read-btn:hover {\r\n    color: #212529 !important;\r\n}\r\n\r\n\r\n/*======================\r\n  4.NAVBAR\r\n  ======================*/\r\n\r\n\r\n.navbar-custom {\r\n    padding: 20px 0px;\r\n    width: 100%;\r\n    border-radius: 0px;\r\n    z-index: 999;\r\n    margin-bottom: 0px;\r\n    transition: all 0.5s ease-in-out;\r\n}\r\n\r\n\r\n.sticky-wrapper {\r\n    position: absolute;\r\n    width: 100%;\r\n}\r\n\r\n\r\n.navbar-nav {\r\n    margin-left: 50px;\r\n    border-bottom: none;\r\n}\r\n\r\n\r\n.navbar-custom .navbar-brand {\r\n    text-transform: uppercase;\r\n    color: #fff !important;\r\n    font-weight: 700;\r\n    letter-spacing: 2px;\r\n    height: auto;\r\n    font-family: 'Sarabun', sans-serif;\r\n}\r\n\r\n\r\n.navbar-custom .navbar-nav li a {\r\n    color: rgba(255, 255, 255, 0.6);\r\n    font-size: 15px;\r\n    background-color: transparent !important;\r\n    padding: 15px 0;\r\n    margin: 0 10px;\r\n    transition: all 0.4s;\r\n}\r\n\r\n\r\n.navbar-custom .navbar-nav li a:hover,\r\n.navbar-custom .navbar-nav li a:focus,\r\n.navbar-custom .navbar-nav li a:active {\r\n    color: #fff;\r\n}\r\n\r\n\r\n.navbar-custom .navbar-nav li.active a {\r\n    color: #fff;\r\n}\r\n\r\n\r\n.is-sticky .navbar-custom {\r\n    margin-top: 0px;\r\n    padding: 10px 0px;\r\n    background-color: #272a33;\r\n    box-shadow: 0 10px 33px rgba(0, 0, 0, .1);\r\n}\r\n\r\n\r\n.navbar-toggle {\r\n    font-size: 24px;\r\n    margin-top: 0px;\r\n    margin-bottom: 0px;\r\n    color: #ffffff;\r\n}\r\n\r\n\r\n.navbar-custom .btn-custom {\r\n    margin-top: 4px;\r\n    margin-bottom: 4px;\r\n}\r\n\r\n\r\n.nav .open>a,\r\n.nav .open>a:focus,\r\n.nav .open>a:hover {\r\n    background-color: transparent;\r\n    border-color: #337ab7;\r\n}\r\n\r\n\r\n/*---Navbar-White--*/\r\n\r\n\r\n.navbar-custom.navbar-white {\r\n    background-color: #fff;\r\n    padding: 10px 0;\r\n    box-shadow: 0 10px 33px rgba(0, 0, 0, .1);\r\n}\r\n\r\n\r\n.navbar-custom.navbar-white .navbar-nav li a,\r\n.navbar-custom.navbar-white .navbar-brand,\r\n.navbar-white .navbar-toggle {\r\n    color: #393f4f !important;\r\n}\r\n\r\n\r\n/*=======================================\r\n          5.HOME \r\n  =====================================*/\r\n\r\n\r\n.bg-home {\r\n    background: url('bg-home.jpg');\r\n    background-repeat: no-repeat;\r\n    background-size: cover;\r\n    background-position: center;\r\n}\r\n\r\n\r\n.home-title {\r\n    font-size: 46px;\r\n    line-height: 64px;\r\n}\r\n\r\n\r\n.home-small-title {\r\n    font-size: 16px;\r\n    text-transform: uppercase;\r\n    letter-spacing: 7px;\r\n}\r\n\r\n\r\n.display-table-cell {\r\n    display: table-cell;\r\n    vertical-align: middle;\r\n}\r\n\r\n\r\n.display-table {\r\n    display: table;\r\n    width: 100%;\r\n    height: 100%;\r\n}\r\n\r\n\r\n.home-up {\r\n    z-index: 10;\r\n    position: relative;\r\n}\r\n\r\n\r\n.play-btn {\r\n    border: 2px solid;\r\n    border-radius: 50%;\r\n    width: 60px;\r\n    height: 60px;\r\n    display: block;\r\n    font-size: 30px;\r\n    margin-right: auto;\r\n    margin-left: auto;\r\n    line-height: 60px;\r\n    margin-top: 10px;\r\n    text-align: center;\r\n}\r\n\r\n\r\n.play-shadow {\r\n    border-radius: 50%;\r\n    border: 1px solid transparent;\r\n    width: 82px;\r\n    height: 82px;\r\n    margin: 20px auto 0px auto;\r\n}\r\n\r\n\r\n.home-desc {\r\n    color: rgba(255, 255, 255, 0.7);\r\n    font-size: 15px;\r\n    max-width: 600px;\r\n}\r\n\r\n\r\n/* ------------- Demo 1 bg-pattern ------------- */\r\n\r\n\r\n.home-half {\r\n    padding-bottom: 150px;\r\n    padding-top: 220px;\r\n}\r\n\r\n\r\n/* ------- Demo 9 wave animation --------- */\r\n\r\n\r\n.wave-effect {\r\n    position: absolute;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    margin: auto;\r\n    height: 150px;\r\n    overflow: hidden;\r\n}\r\n\r\n\r\n.shape-one {\r\n    z-index: 15;\r\n    opacity: 0.5;\r\n}\r\n\r\n\r\n.shape-two {\r\n    z-index: 10;\r\n    opacity: 0.75;\r\n}\r\n\r\n\r\n.shape-three {\r\n    z-index: 5;\r\n}\r\n\r\n\r\n.wave {\r\n    position: absolute;\r\n    left: 0;\r\n    width: 200%;\r\n    height: 100%;\r\n    background-repeat: repeat no-repeat;\r\n    background-position: 0 bottom;\r\n    transform-origin: center bottom;\r\n}\r\n\r\n\r\n.waves-shape {\r\n    position: absolute;\r\n    bottom: -1px;\r\n    width: 100%;\r\n    height: 100%;\r\n    overflow: hidden;\r\n}\r\n\r\n\r\n.wave-one {\r\n    background-size: 50% 100px;\r\n}\r\n\r\n\r\n.wave-anim .wave-one {\r\n  animation: move-wave 3s;\r\n   -webkit-animation: move-wave 3s;\r\n   -webkit-animation-delay: 1s;\r\n   animation-delay: 1s;\r\n}\r\n\r\n\r\n.wave-two {\r\n    background-size: 50% 120px;\r\n}\r\n\r\n\r\n.wave-anim .wave-two {\r\n    -webkit-animation: move_wave 10s linear infinite;\r\n            animation: move_wave 10s linear infinite;\r\n}\r\n\r\n\r\n.wave-three {\r\n    background-size: 50% 100px;\r\n}\r\n\r\n\r\n.wave-anim .wave-three {\r\n    -webkit-animation: move_wave 15s linear infinite;\r\n            animation: move_wave 15s linear infinite;\r\n}\r\n\r\n\r\n@-webkit-keyframes move_wave {\r\n    0% {\r\n        transform: translateX(0) translateZ(0) scaleY(1)\r\n    }\r\n    50% {\r\n        transform: translateX(-25%) translateZ(0) scaleY(0.55)\r\n    }\r\n    100% {\r\n        transform: translateX(-50%) translateZ(0) scaleY(1)\r\n    }\r\n}\r\n\r\n\r\n@keyframes move_wave {\r\n    0% {\r\n        transform: translateX(0) translateZ(0) scaleY(1)\r\n    }\r\n    50% {\r\n        transform: translateX(-25%) translateZ(0) scaleY(0.55)\r\n    }\r\n    100% {\r\n        transform: translateX(-50%) translateZ(0) scaleY(1)\r\n    }\r\n}\r\n\r\n\r\n/*-------Demo 4 Rgistartion form ------*/\r\n\r\n\r\n.home-registration-form .form-heading {\r\n    margin-bottom: 25px;\r\n}\r\n\r\n\r\n.home-registration-form {\r\n    padding: 15px 32px 32px 32px;\r\n    border-radius: 3px;\r\n    box-shadow: 0px 0px 3px 0px #000000;\r\n}\r\n\r\n\r\n.home-subtitle-width-100 {\r\n    max-width: 100%;\r\n}\r\n\r\n\r\n.small-text {\r\n    font-size: 14px;\r\n}\r\n\r\n\r\n.home-btn-width {\r\n    width: 100%;\r\n}\r\n\r\n\r\n/*----Demo 5 Dashboard image ----*/\r\n\r\n\r\n.home-cell-bottom {\r\n    display: table-cell;\r\n    vertical-align: bottom;\r\n}\r\n\r\n\r\n.home-padding-t-150 {\r\n    padding-top: 150px;\r\n}\r\n\r\n\r\n/*---Demo 6 Subscribe -----*/\r\n\r\n\r\n.subscribe-form input {\r\n    /*padding: 15px 20px;*/\r\n    width: 100%;\r\n    font-size: 17px;\r\n    color: #4c5667 !important;\r\n    border: none;\r\n    outline: none !important;\r\n    padding-right: 180px;\r\n    padding-left: 30px;\r\n    background-color: rgba(255, 255, 255, 0.85);\r\n    border-radius: 30px;\r\n}\r\n\r\n\r\n.subscribe-form but